import humanId from "human-id"
import range from "lodash/range"
import {
  createGameServer,
  createRuneSDKForLogicRunner,
  RunGameLogic,
} from "rune-games-sdk/dist/multiplayer/gameServer"
import { PlayerId, Players } from "rune-games-sdk/multiplayer"
import * as uuid from "uuid"

import { emitter } from "./emitter"
import { createPromiseQueue } from "./queue"

type User = {
  id: number
  playerId: PlayerId
  displayName: string
  spectator: boolean
}

export const users: User[] = []

function getPlayers() {
  return users
    .filter((user) => !user.spectator)
    .reduce<Players>(
      (acc, user) => ({
        ...acc,
        [user.playerId]: {
          playerId: user.playerId,
          displayName: user.displayName,
        },
      }),
      {}
    )
}

// only call this after the game client state is PLAYING
export function isSpectator(userId: number) {
  return !!users.find((user) => user.id === userId)?.spectator
}

const queue = createPromiseQueue()

export async function startGameServer(gameUrl: string) {
  const logicJsUrl = new URL("logic.js", gameUrl).toString()

  const logicJs = await fetch(logicJsUrl).then((r) => r.text())

  const Rune = createRuneSDKForLogicRunner()

  eval(logicJs)

  const { setup, events, actions, context: runeContext, gameConfig } = Rune

  if (!setup || !events || !actions || !gameConfig) {
    throw new Error("sdk init failed")
  }

  const logicRunner: RunGameLogic = {
    getConfig: () => gameConfig,
    runSetup: async (_, players) => setup(players),
    runEvent: async (_, event, params, context) => {
      const eventHandler = events[event]

      if (!eventHandler) {
        throw new Error("INVALID EVENT!")
      }

      return {
        game: eventHandler(params, context),
        context: runeContext,
      }
    },
    runAction: async (_, action, ...args) => ({
      game: actions[action](...args),
      context: runeContext,
    }),
  }

  const msgLogger = console

  function addUser({
    userId,
    spectator,
  }: {
    userId: number
    spectator: boolean
  }) {
    const playerId = uuid.v4()

    users.push({
      id: userId,
      playerId,
      displayName: humanId({
        adjectiveCount: 0,
        capitalize: false,
        separator: "-",
      }),
      spectator,
    })

    return playerId
  }

  function removePlayer(userId: number) {
    const userIndex = users.findIndex((user) => user.id === userId)
    const user = users[userIndex]

    users.splice(userIndex, 1)

    return user.playerId
  }

  // pre-create users to match minPlayers, frontend will also pre-create player views
  range(0, gameConfig.minPlayers).map(() => {
    addUser({ userId: Math.round(Math.random() * 1e5), spectator: false })
  })

  const gameServer = await createGameServer({
    initialMsgLogger: msgLogger,
    network: {
      getPlayers,
      broadcastMsg: (_, msg) => emitter.emit("broadcastMsg", { msg }),
      sendMsg: (_, playerId, msg) => {
        const userId = users.find((user) => user.playerId === playerId)?.id

        if (!userId) throw new Error("user not found")

        emitter.emit("emitMsg", { userId, msg })
      },
    },
    logic: logicRunner,
    initialGameStateAndContext: null,
  })

  emitter.addListener("connect", ({ userId }) => {
    queue(async () => {
      // in case this is a pre-created frontend player view trying to "connect"
      if (users.find((user) => user.id === userId)) return

      const playerCount = Object.keys(getPlayers()).length

      // todo: maybe better to handle this inside SDK?
      const spectator =
        playerCount >= gameConfig.maxPlayers ||
        (!gameConfig.eventCallbacks.playerJoined &&
          playerCount >= gameConfig.minPlayers)

      const playerId = addUser({ userId, spectator })

      if (!spectator) await gameServer.onPlayerJoined(msgLogger, playerId)
    })
  })

  emitter.addListener("disconnect", ({ userId }) => {
    queue(async () => {
      const playerId = removePlayer(userId)

      await gameServer.onPlayerLeft(msgLogger, playerId)
    })
  })

  emitter.addListener("onMsg", ({ userId, msg }) => {
    queue(async () => {
      const playerId = users.find((user) => user.id === userId)?.playerId

      if (!playerId) throw new Error("player not found")

      await gameServer.onMsg(msgLogger, playerId, msg)
    })
  })

  return gameConfig
}
