import React from "react"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import { latencyOptions } from "../../AppMultiplayerCLI"
import { webTheme } from "../../styles/webTheme"
import { Modal } from "../Settings/Modal"


export function MultiplayerSettingsModal({
  latency,
  setLatency,
  onClose,
}: {
  latency: number
  setLatency: (latency: number) => void
  onClose: () => void
}) {
  return (
    <Modal onClose={onClose}>
      <Root>
        <label>
          <span>{skipTr("Simulated latency:")}</span>
          <select value={latency} onChange={(e) => setLatency(+e.target.value)}>
            {latencyOptions.map((latency) => (
              <option key={latency} value={latency}>
                {skipTr(`${latency}ms`)}
              </option>
            ))}
          </select>
        </label>
      </Root>
    </Modal>
  )
}

const Root = styled.div`
  padding: 20px 0;
  ${webTheme.text.label}
  > label {
    > :not(:first-child) {
      margin-left: 5px;
    }
  }
`
