import { useEffect, useRef, useMemo, useState } from "react"
import { createGameClient } from "rune-games-sdk/dist/multiplayer/gameClient"
import { GameConfig, GameOverContext } from "rune-games-sdk/multiplayer"
import styled from "styled-components/macro"

import { skipTr } from "lib/lingui"

import { flex } from "../lib/flex"
import { emitter } from "../lib/server/emitter"
import { isSpectator } from "../lib/server/gameServer"
import { webTheme } from "../styles/webTheme"

import { GameViewErrorHandler } from "./GameView/GameView"

export function MultiplayerGameView({
  gameConfig,
  gameUrl,
  userId,
  onError,
  onGameOver,
  onSpectator,
}: {
  gameConfig: GameConfig
  gameUrl: string
  userId: number
  onError: GameViewErrorHandler
  onGameOver: (context: GameOverContext) => void
  onSpectator: () => void
}) {
  const iframeRef = useRef<HTMLIFrameElement | null>(null)
  const rootRef = useRef<HTMLDivElement | null>(null)
  const [spectatorExplainer, setSpectatorExplainer] = useState<
    "maxPlayersExceeded" | "playerJoinedUndefined" | false
  >(false)

  const onSpectatorRef = useRef(onSpectator)
  onSpectatorRef.current = onSpectator

  const onErrorRef = useRef(onError)
  onErrorRef.current = onError

  const onGameOverRef = useRef(onGameOver)
  onGameOverRef.current = onGameOver

  const gameClient = useMemo(
    () =>
      createGameClient({
        network: {
          sendMsgToServer: (msg) => {
            emitter.emit("onMsg", { userId, msg })
          },
          sendMsgToGame: (msg) => {
            iframeRef.current?.contentWindow?.postMessage(msg, "*")
          },
        },
        onGameToClientPayload: (payload) => {
          switch (payload.type) {
            case "ERR":
              onErrorRef.current(payload.errMsg, { fatal: true })
              break
            case "WINDOW_ERR":
              onErrorRef.current(payload.err.msg, { fatal: true })
              break
          }
        },
        onGameStateChanged: (args) => {
          if (args.state === "GAME_OVER") onGameOverRef.current(args.context)

          if (args.state === "PLAYING") {
            if (isSpectator(userId)) {
              onSpectatorRef.current()
              setSpectatorExplainer(
                gameConfig.eventCallbacks.playerJoined
                  ? "maxPlayersExceeded"
                  : "playerJoinedUndefined"
              )
            } else {
              setSpectatorExplainer(false)
            }
          }
        },
      }),
    [gameConfig.eventCallbacks.playerJoined, userId]
  )

  useEffect(() => {
    emitter.emit("connect", { userId })

    const unsubscribeCallbacks = [
      emitter.addListener("emitMsg", (event) => {
        if (event.userId === userId) gameClient.onServerMsg(event.msg)
      }),
      emitter.addListener("broadcastMsg", (event) => {
        gameClient.onServerMsg(event.msg)
      }),
    ]

    return () => {
      emitter.emit("disconnect", { userId })

      unsubscribeCallbacks.forEach((unsubscribe) => unsubscribe())
    }
  }, [gameClient, userId])

  useEffect(() => {
    function onMessage(e: MessageEvent<unknown>) {
      if (
        e.source === iframeRef.current?.contentWindow &&
        typeof e.data === "string"
      ) {
        gameClient.onGameMsg(e.data)
      }
    }

    window.addEventListener("message", onMessage)

    return () => {
      window.removeEventListener("message", onMessage)
    }
  }, [gameClient])

  return (
    <Root ref={rootRef}>
      <iframe ref={iframeRef} src={gameUrl} width="100%" height="100%" />
      {spectatorExplainer && (
        <Overlay>
          <span>
            {skipTr(
              spectatorExplainer === "maxPlayersExceeded"
                ? `This roommate is a spectator as the number of roommates is higher than maxPlayers (${gameConfig.maxPlayers})`
                : `This roommate is a spectator as your game doesnt support the playerJoined event. If youd like to allow players to join mid-game, please define a callback for the playerJoined event.`
            )}
          </span>
          <div onClick={() => setSpectatorExplainer(false)}>
            {skipTr("Close")}
          </div>
        </Overlay>
      )}
    </Root>
  )
}

const Root = styled.div`
  position: relative;
  height: 100%;
  width: 100%;
`

const Overlay = styled.div`
  background-color: #000000b0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  padding: 20px;
  ${flex({ vAlign: "center", hAlign: "center" })};
  user-select: none;

  > :not(:first-child) {
    margin-top: 10px;
  }

  > span {
    ${webTheme.text.body};
    color: white;
    text-shadow: 0 0 10px black;
  }

  > div {
    ${webTheme.text.labelBold};
    background-color: white;
    border-radius: 5px;
    padding: 3px 6px;
    cursor: pointer;
  }
`
