import React, { useState, useEffect, useCallback } from "react"
import { useMediaQuery } from "react-responsive"
import { GameConfig, GameOverContext } from "rune-games-sdk/multiplayer"

import { GameOverModal } from "./components/Multiplayer/GameOverModal"
import { MultiplayerViewDesktop } from "./components/Multiplayer/MultiplayerViewDesktop"
import { MultiplayerViewMobile } from "./components/Multiplayer/MultiplayerViewMobile"
import { GameErrorModal } from "./components/Settings/GameErrorModal"
import { emitter } from "./lib/server/emitter"
import { startGameServer, users } from "./lib/server/gameServer"
import { CLIData } from "./lib/useCLIData"
import { GameViewErrorHandler } from "./views/GameView/GameView"

export const latencyOptions = [0, 50, 250, 500, 2000]
export const maxPlayers = 4

export type PlayerView = {
  userId: number
  spectator?: boolean
}

export function AppMultiplayerCLI({ data }: { data: CLIData }) {
  const [gameConfig, setGameConfig] = useState<GameConfig | undefined>()
  const [latency, setLatency] = useState(50)
  const [errorMessages, setErrorMessages] = useState<string[]>([])
  const isMobile = useMediaQuery({ query: "(max-width: 992px)" })
  // todo: SDK should report reason for game over
  const [gameOver, setGameOver] = useState<GameOverContext | false>(false)

  useEffect(() => {
    emitter.latency = latency
  }, [latency])

  useEffect(() => {
    startGameServer(data.gameUrl).then(setGameConfig)
  }, [data.gameUrl])

  const [playerViews, setPlayerViews] = useState<PlayerView[]>([])

  const addPlayerView = useCallback(() => {
    setPlayerViews((players) => [
      ...players,
      { userId: Math.round(Math.random() * 1e5) },
    ])
  }, [])

  const setPlayerViewSpectator = (i: number) => {
    playerViews[i].spectator = true
    setPlayerViews([...playerViews])
  }

  const onRemovePlayerView = (i: number) => {
    playerViews.splice(i, 1)
    setPlayerViews([...playerViews])
  }

  useEffect(() => {
    if (!gameConfig) return
    // pre-create player views for already pre-created users
    setPlayerViews(users.map((user) => ({ userId: user.id })))
  }, [gameConfig])

  const onError = useCallback<GameViewErrorHandler>(
    (errMsg) => setErrorMessages((prev) => [...prev, errMsg]),
    []
  )

  const onErrorModalClose = useCallback(() => {
    setErrorMessages([])
  }, [])

  const onErrorModalReload = useCallback(() => {
    setErrorMessages([])
    window.location.reload()
  }, [])

  const onGameOver = useCallback((context: GameOverContext) => {
    setGameOver(context)
  }, [])

  if (!gameConfig) return null

  const MultiplayerView = isMobile
    ? MultiplayerViewMobile
    : MultiplayerViewDesktop

  return (
    <>
      <MultiplayerView
        gameConfig={gameConfig}
        latency={latency}
        setLatency={setLatency}
        playerViews={playerViews}
        data={data}
        addPlayerView={addPlayerView}
        onError={onError}
        onGameOver={onGameOver}
        onSpectator={setPlayerViewSpectator}
        onRemove={onRemovePlayerView}
      />
      <GameErrorModal
        messages={errorMessages}
        onClose={onErrorModalClose}
        onReload={onErrorModalReload}
      />
      {gameOver && <GameOverModal gameConfig={gameConfig} context={gameOver} />}
    </>
  )
}
